import Vue from 'vue'
import VueRouter from "vue-router"
import App from './App.vue'
import router from './router'
import { library } from '@fortawesome/fontawesome-svg-core'
import { faMapMarkerAlt, faEnvelope, faFileAlt, faVideo, faFilePowerpoint } from '@fortawesome/free-solid-svg-icons'
import { faGithub, faOrcid } from '@fortawesome/free-brands-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

library.add(faMapMarkerAlt, faEnvelope, faFileAlt, faVideo, faFilePowerpoint, faGithub, faOrcid)
Vue.component('font-awesome-icon', FontAwesomeIcon)

Vue.config.productionTip = false
Vue.use(VueRouter)

new Vue({
  render: h => h(App),
  router: router
}).$mount('#app')
