<template>
  <div class="pub">
    <p class="title">{{ title }}</p>
    <p class="conference">{{ conference }}</p>
    <div class="author-list">
      <p :key="index" v-for="(author, index) in authors">
        <span v-if="author.name === 'Jiaming Qiu'" class="self">{{
          author.name
        }}</span>
        <Link
          v-else-if="author.link !== ''"
          :path="author.link"
          :text="author.name"
        />
        <span v-else>{{ author.name }}</span>
        <span v-if="index != Object.keys(authors).length - 1">,&nbsp;</span>
      </p>
    </div>
    <div class="author-list">
      <div v-if="paper !== ''">
        <font-awesome-icon class="icon-style" :icon="['fas', 'file-alt']" />
        <Link :path="paper" text="Paper" />
      </div>
      <div v-if="video !== ''">
        <font-awesome-icon class="icon-style" :icon="['fas', 'video']" />
        <Link :path="video" text="Video" />
      </div>
      <div v-if="slides !== ''">
        <font-awesome-icon
          class="icon-style"
          :icon="['fas', 'file-powerpoint']"
        />
        <Link :path="slides" text="Slides" />
      </div>
    </div>
  </div>
</template>

<script>
import Link from "./Link";

export default {
  name: "Publication",
  props: {
    title: String,
    conference: String,
    authors: Array,
    paper: String,
    video: String,
    slides: String,
  },
  components: {
    Link,
  },
};
</script>

