div.pub {
  margin: 0 0 30px;
}
div.author-list {
  display: flex;
}
div.author-list > div {
  margin-right: 40px;
}
p {
  margin-top: 0;
  margin-bottom: 6px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: #212325;
  font-size: 20px;
  line-height: 150%;
}
p.title {
  font-weight: bold;
}
p.conference {
  font-size: 16px;
}
span {
  font-size: 16px;
}
span.self {
  color: #006699;
}
a {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: #66ccff;
  font-size: 20px;
}
.icon-style {
  margin-right: 1em;
  color: #212325;
  font-size: 20px;
}
@media (max-width: 1000px) {
